.header-container {
  position: relative;
}

.header-container:after {
  content: "";
  z-index: 15;
  height: calc(var(--index) * 18);
  bottom: calc(var(--index) * -8);
  filter: drop-shadow(0 10px 10px #00000080);
  background-image: url("after-header.6ed0ccf6.webp");
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  display: inline-block;
  position: absolute;
}

.header-container .layers {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
  position: relative;
  overflow: hidden;
}

.header-container .layer {
  will-change: transform;
  z-index: 2;
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 100%;
  position: absolute;
}

.header-container .layer__base {
  transform: translate3d(0, calc(var(--scrollTop) / 1.5), 0);
  z-index: 0;
  background-image: url("layer-base.f81cea97.webp");
}

.header-container .layer__middle {
  transform: translate3d(0, calc(var(--scrollTop) / 2.5), 0);
  background-image: url("layer-middle.4db8567a.webp");
}

.header-container .layer__front {
  transform: translate3d(0, calc(var(--scrollTop) / 8), 0);
  background-image: url("layer-front.c665517a.webp");
}

.header-container .layer__header {
  z-index: 1;
  transform: translate3d(0, calc(var(--scrollTop) / 1.2), 0);
}

.header-container .layer__caption {
  position: relative;
  transform: translateY(-100%);
}

.header-container .title--primary, .header-container .title--secondary {
  color: var(--color-nearly-white);
  text-shadow: 0 0 2px #000;
}

.header-container .scroll-down-btn--link {
  z-index: 15;
  border: 2px solid var(--color-accent--lightest-trasparent);
  outline: 1px solid var(--color-accent--lightest-trasparent);
  border-radius: 50%;
  width: 4.2rem;
  height: 4.2rem;
  transition: all .3s ease-in-out;
  display: inline-block;
  position: absolute;
  bottom: 35svh;
  left: 50%;
  transform: translateX(-50%);
}

.header-container .scroll-down-btn--img {
  object-fit: contain;
  width: 100%;
  height: 100%;
}
